* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, sans-serif;
}
  
body {
    background-color: #f4f4f9;
    color: #333;
    line-height: 1.6;
    padding: 20px;
}
  
nav {
    background-color: #333;
    color: #fff;
    padding: 10px;
}
  
nav ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
}
  
nav a {
    color: white;
    text-decoration: none;
}
  
nav a:hover {
    text-decoration: underline;
}
  
h1, h2 {
    margin-bottom: 10px;
    text-align: center;
}
  
form {
    background-color: #fff;
    padding: 20px;
    border: 1px solid #ddd;
    margin-bottom: 20px;
    border-radius: 5px;
}
  
label {
    display: block;
    margin-bottom: 10px;
}
  
input[type="text"],
input[type="date"],
input[type="time"],
textarea,
select {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
}
  
button {
    background-color: #333;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
}
  
button:hover {
    background-color: #555;
}
  
blockquote {
    background-color: #fff;
    padding: 10px;
    border-left: 5px solid #333;
    margin: 10px 0;
}
  
footer {
    font-size: 0.8em;
    color: #777;
}
  
ul {
    list-style: none;
    padding: 0;
}
  
li {
    padding: 10px;
    margin-bottom: 5px;
    border-radius: 5px;
}